@import "../../styles/variables";
@import "../../styles/media";

.menu {
  width: 285px;
  background-color: $white;
  box-shadow: 0px 1px 75px rgba(90, 97, 105, 0.11), 0px 2px 4px rgba(90, 97, 105, 0.12), 0px 7.5px 11px rgba(90, 97, 105, 0.1), 0px 3.5px 17.5px rgba(165, 182, 201, 0.1);
  position: relative;
  z-index: 1;
  flex-shrink: 0;
  display: flex;
  flex-direction: column; }

.logo {
  padding: $padding $padding * 2; }

.navigation {
  flex-grow: 1;
  overflow: hidden; }

.link {
  font-size: 15px;
  line-height: 17px;
  letter-spacing: -0.234375px;
  text-decoration: none;
  display: flex;
  align-items: center; }

.logotype {
  width: 21.5px;
  height: 21.5px;
  margin-right: 7px; }

.name {
  font-size: 16px;
  font-weight: normal;
  line-height: 18px; }

.list {
  list-style-type: none;
  height: 100%;
  padding: 0;
  margin: 0;
  overflow-y: auto; }

.item {
  position: relative;
  &::before {
    content: "";
    width: 100%;
    height: 1px;
    background-color: $ultraLightGrey;
    position: absolute;
    top: 0;
    left: 0; }
  &:last-of-type {
    &::after {
      content: "";
      width: 100%;
      height: 1px;
      background-color: $ultraLightGrey;
      position: absolute;
      bottom: 0;
      left: 0; } }
  .link {
    padding: 20px $padding * 2;
    position: relative;
    user-select: none;
    transition: all $short ease;
    &:hover {
      background-color: $ligthGrey; }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 4px;
      height: 100%;
      background-color: $brightBlue;
      transform: translateX(-100%);
      transition: all $short ease; }
    svg {
      position: absolute;
      top: 50%;
      left: 28px;
      transform: translateY(-50%);
      path {
        fill: $lightBlue;
        transition: all $short ease; } } } }

.active.link {
  color: $brightBlue;
  background-color: $classicGrey;
  &::before {
    transform: translateX(0%); }
  svg {
    path {
      fill: $brightBlue; } } }

@include desktopMinDevice {
  .menu {
    width: 252px; } }

@include tabletDevice {
  .menu {
    transform: translateX(-100%);
    position: absolute;
    z-index: 3;
    height: 100vh;
    transition: all $short ease-in-out;
    box-shadow: none; }
  .showMenu {
    box-shadow: 0px 1px 75px rgba(90, 97, 105, 0.11), 0px 2px 4px rgba(90, 97, 105, 0.12), 0px 7.5px 11px rgba(90, 97, 105, 0.1), 0px 3.5px 17.5px rgba(165, 182, 201, 0.1);
    transform: translateX(0); } }

@include mobileDevice {
  .logo {
    max-height: 67px;
    box-sizing: border-box; } }

